body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  margin: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center; }

.app_header-title {
  text-align: center; }

.app_main {
  margin: 0;
  width: 100%; }

.input_container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  margin-bottom: 25px;
  width: 800px; }

.pokemon_filtered-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  list-style: none; }

.app_field-input {
  width: 200px; }

.pokemon_card {
  background-color: #e0e0f5;
  width: 200px;
  height: 250px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  padding-bottom: 15px;
  transition: color ease 1s, border-color ease 1s; }
  .pokemon_card:hover {
    color: red;
    background-color: #ece2f6;
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25); }

.list_item-link {
  text-decoration: none; }

.link_return {
  text-decoration: none;
  color: red;
  margin-top: 20px;
  text-transform: uppercase; }

.app_main-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column; }

.pokemon_type {
  list-style: none; }

